<template>
    <div class="wrapper">
        <div id="certificate-item" class="certificate">
            <div class="certificate__wrapper">

                <h2 style="margin-top: 223px;" class="bold name">{{fullname}}</h2>

            </div>
        </div>
        <br/>
        <button class="btn__grey" @click="print">Print</button>
    </div>
</template>

<script>
  import domToImage from 'dom-to-image';
  import * as JsPdf from 'jspdf';

  export default {
    name: 'Certificate',
    props: {
      title: {
        type: String,
        default(){
          return '';
        }
      },
      score: {
        type: Number,
        default(){
          return null;
        }
      }
    },
    data(){
      return {
        imgUrl: '',
        fname: this.$session.get('fname'),
        lname: this.$session.get('lname')
      };
    },
    computed: {
      fullname(){
        return `${this.fname} ${this.lname}`;
      }
    },
    mounted(){

    },
    methods: {
      print(){
        domToImage.toPng(document.getElementById('certificate-item'), {quality: 0.95})
          .then((dataUrl) => {
            this.imgUrl = dataUrl;
            const doc = new JsPdf('l', '', 'a4');
            doc.addImage(dataUrl, 'PNG', 0, 0, 297, 210);
            doc.save('certificate.pdf');
          })
          .catch((error) => {
            return error;
          });
        return true;
      }
    }
  };
</script>

